<template>
    <v-scroll-view class="demo-page flex">
        <v-popup-picker :is-show.sync="isShow" :data-source="[data1, data2]" v-model="value"/>
        <section class="flex-item--center">
            <p class="text-center">当前索引: {{value}}</p>
            <v-button type="primary" @click="isShow=true" class="gutter-top">打开</v-button>
        </section>
    </v-scroll-view>
</template>
<script>
import VPopupPicker from '@/packages/PopupPicker/PopupPicker';
import VButton from '@/packages/Button/Button';
import VScrollView from '@/packages/ScrollView/ScrollView';
export default {
    name: 'PopupPickerDemo',

    data() {
        let data2 = [];
        let i = 0;
        while (i <= 12) {
            i++;
            data2.push({ value: i, label: i + '月份' });
        }
        return {
        isShow: false,
            data1: [{ value: 1, label: '休息' }, { value: 2, label: '出差' }],
            data2: data2,
            value: [1, 2]
        };
    },

    methods: {},

    computed: {},

    components: {
        VButton,
        VPopupPicker,
        VScrollView
    }
};
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
.demo-page {
    height: calc(100% - 55px);
}
</style>



